@import "colors";

.about {
    width: 100%;
    background-color: $pink;

    >main {
        background-color: white;
        max-width: 900px;
        height: 100%;
        margin: auto;

        >h1 {
            font: 300 3rem "Roboto";
            color: $red;
            text-transform: uppercase;
            background-color: $dark;
            text-align: center;
        }

        >article {
            margin: 2rem 0;

            >h4 {
                font: 100 2rem "Roboto";
                text-transform: uppercase;
                text-align: center;
                padding: 2rem 0;
                font-style: oblique;
            }

            >p {
                letter-spacing: 3px;
                word-spacing: 3px;
                padding: 2rem;
                width: 50%;
                text-align: center;
                margin: auto;
            }

            >a {
                background-color: $dark;
                border-radius: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                width: 2.5rem;
                height: 2.5rem;
                color: white;
                font-size: 1.5rem;
                transition: all 0.3s;
                margin: auto;

                &:hover {
                    color: $red;
                    transform: scale(1.5);
                }

            }
        }

        >div {
            background-color: $dark;
            padding: 2rem;
            color: white;

            >h2 {
                text-align: center;
                text-transform: uppercase;
            }

            >article {
                display: flex;
                align-items: center;
                justify-content: center;

                >div {
                    margin: 2rem;

                    >img {
                        width: 150px;
                        height: 150px;
                        border-radius: 100%;
                    }

                    >h3 {
                        margin-left: 3rem 3rem;
                    }
                }

                >div {

                    >p {
                        margin: 0 1rem;
                        width: 100%;
                        letter-spacing: 2px;
                        line-height: 150%;
                        font-style: oblique;
                    }
                }
            }
        }
    }
}

@media screen and (max-width:600px) {

    .about>main {
        >h1 {
            font: 200 3rem "Roboto";
            text-align: center;
        }

        >article >p{
            width: 100%;
        }

        >div >article{
            flex-direction: column;

            >div >h3{
                margin-top: 1rem;
            }
            >p{
                text-align: center;
            }
        }


    }
}