@import "colors";

.confirmOrder {
    background-color: $pink;
    width: 100%;
    height: 60vh;

    >main {
        height: 100%;
        background-color: white;
        max-width: 900px;
        margin: auto;
        padding: 2rem;

        >h1 {
            text-transform: uppercase;
            text-align: center;
            margin: 3rem;
            font: 100 3rem "Roboto";
        }

        >form {
            display: flex;
            flex-direction: column;
            align-items: center;

            >div {
                background-color: rgb(233, 233, 233);
                margin: 1rem;
                padding: 1rem;
                border-radius: 5px;
                width: 70%;
                display: flex;
                align-items: center;
                justify-content: space-between;


            }

            >button {
                background-color: $red;
                color: white;
                outline: none;
                border: none;
                padding: 0.5rem 1rem;
                border-radius: 5px;
                margin: 1rem;
                cursor: pointer;
                text-align: center;
                font-size: 1.1rem;
            }
        }
    }
}

@media screen and (max-width:600px) {
    .confirmOrder {
        height: fit-content;

        >main>form {
            >div {
                width: 100%;
            }

            >button {
                width: 100%;
                margin-bottom: 20px;
            }
        }
    }
}