@import "colors";

nav{
    height: 5rem;
    padding: 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-shadow: 0 0 10px rgba(0,0,0,0.229);
    background-color: white;
    
    width: 100%;
    z-index: 1;

    >div{
       display: flex;
       align-items: center;
       
       >svg{
        color: $red;
        font-size: 3rem;
       }

       >a{
        margin: 0 2rem;
        font-size: 1.2rem;
        color: $dark;
        transition: color 0.3s;  
        &:hover{
            color: $lightdark;
        } 
        
        >svg{
            color: $red;
            transition: inherit;
            font-size: 1.5rem;
            &:hover{
                color: $lightdark;
            }
        }
       }
    }
}

@media screen and (max-width:600px) {
    nav{
        padding: 1rem;
        >div>a{
            margin: 0 0.5rem;
        }
    }
}